
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'IamPopup',

  props: {
    active: {
      type: Boolean,
      required: false,
      default: false,
    },
    closeBtn: {
      type: Boolean,
      required: false,
      default: true,
    },
  },

  methods: {
    onClick() {
      this.$emit('update:active');
    },
  },
});
