
import IamPopup from '@/components/Global/IamPopup/IamPopup.vue';
import IamButton from '@/components/Global/IamButton.vue';
import { defineComponent } from 'vue';
import Cookies from 'js-cookie';

export default defineComponent({
  name: 'IamLocation',
  components: {
    IamPopup,
    IamButton,
  },
  data: () => ({
    popupOpen: false,
    town: 0,
    citys: [
      {
        id: 'city-1',
        name: 'Краснодар',
        value: 1,
      },
      {
        id: 'city-2',
        name: 'Москва (скоро)',
        value: 2,
      },
      {
        id: 'city-3',
        name: 'Омск (скоро)',
        value: 3,
      },
      {
        id: 'city-4',
        name: 'Архангельск (скоро)',
        value: 4,
      },
    ],
  }),

  methods: {
    submitForm(e: Event) {
      if (this.town) {
        document.cookie = `city=${this.town}; max-age=604800`;
        this.popupOpen = false;
      }
      e.preventDefault();
    },
    popupClick() {
      this.popupOpen = !this.popupOpen;
    },
  },
  created() {
    // eslint-disable-next-line no-unused-expressions
    Number(Cookies.get('city'))
      ? (this.town = Number(Cookies.get('city')))
      : (this.popupOpen = true);
  },
});
